import server from '../../server.js';
export default {
	data() {
		return {
			thisIndex: 0,
			timeList:[
				{time:'12-06',title:'正在发售'},
				{time:'12-07',title:'正在发售'},
				{time:'12-08',title:'正在发售'},
				{time:'12-09',title:'正在发售'},
				{time:'12-10',title:'正在发售'},
				{time:'12-11',title:'正在发售'}
			],
			goodsList:[]
		}
	},
	onLoad() {
		this.getGoods('')
	},
	methods: {
		go:function(id){
			uni.navigateTo({
				url:'goods?goodsId='+id
			})
			console.log(id)
		},
		tabXZ:function(index){
			console.log(index)
			this.thisIndex=index;
		},
		getGoods:function(date){
			uni.request({
				method:'GET',
				url: server.requestUrl+'getGoodsConcise/'+date, 
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.code=='1'){
						this.goodsList=res.data.data;
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
					console.log(res);
				}
			});
			
			
		}
	}
}
